
import { h, Component } from 'preact';
import style from './style';

import { Check, AlertCircle } from 'preact-feather';
import Quote from "../quote";
import Details from "../details";

let cms = []

for (let i = 1; i < 25; i++){
	cms.push(`CM${i}`);
}

let cos = []

for (let i = 1; i < 17; i++){
	cos.push(`CO${i}`);
}


const ELIGIBLEPOSTCODES = [...cms, ...cos, "SG", "SS"];

const checklitres = (litres="")=>{
	const n = Number(litres);
	
	if (isNaN(n))
		return false;
	return n >= 100 && n <= 2000;
}

const checkpostcode = (postcode="")=>{
	const p = postcode.replace(/\s/g,"").toUpperCase();
	
	return ELIGIBLEPOSTCODES.indexOf(p.toUpperCase()) !== -1;
}

export default class Home extends Component {
	
	state = {
		postcode: "",
		litres: "",
		screen: "home",
	};

	renderLitres(){
			const empty = this.state.litres.trim() === "";
			const valid = checklitres(this.state.litres);
			const icon = valid ?  <Check color={"white"} size={34}/> : <AlertCircle color={"white"} size={34}/>;

			return <div class={style.topcenter}>
						<div class={style.centerleft}>
							<div class={style.label}>how many litres?</div>
							<div class={style.smallabel}>minimum 100, maximum 2000</div>
						</div>
						<div class={style.centerright}>
							<input value={this.state.litres} onInput={e=>this.setState({litres:e.target.value})} placeholder="e.g. 250" class={style.input}></input>
							<div class={style.erroricon}>
								{!empty && icon}
							</div>
						</div>
					</div>
	
	}

	renderPostcode(){
			const empty = this.state.postcode.trim() === "";
			const valid = checkpostcode(this.state.postcode);
			const icon = valid ?  <Check color={"white"} size={34}/> : <AlertCircle color={"white"} size={34}/>;

			return <div class={style.bottomcenter}>
						<div class={style.centerleft}>
							<div class={style.label}>your postcode</div>
							<div class={style.smallabel}>we only need the first part e.g CO1</div>
						</div>
						<div class={style.centerright}>
							<input value={this.state.postcode} onInput={e=>this.setState({postcode:e.target.value})} placeholder="e.g. CO1" class={style.input}></input>
							<div class={style.erroricon}>
								{!empty && icon}
							</div>
						</div>
					</div>
	}

	renderQuote(){
		return <div class={style.back}>
				<Quote litres={this.state.litres}/>
		</div>
	}
	
	renderDetails(){
		return <div class={style.back2}>
				<Details litres={this.state.litres}/>
		</div>
	}

	renderHome(){
		return <div class={style.front}>
				{this.renderLitres()}
				{this.renderPostcode()}
		</div>
	}

	renderContent(){
		const inlinestyle =this.state.screen === "quote" ? {transform: "rotateY(180deg)"} : {}

		return <div class={style.centerbox} style={inlinestyle} >
			
			{this.state.screen==="quote" && this.renderQuote()}
			{this.state.screen==="details" && this.renderDetails()}
			{this.renderHome()}
		</div>
	}

	acceptQuote(){
		return <div class={style.button}>
					<div class={style.buttontext}>
						<div onClick={e=>this.setState({screen:"details"})}>order my oil</div>
					</div>
				</div>
	}

	placeOrder(){
		return <div class={style.button}>
					<div class={style.buttontext}>
						<div onClick={e=>this.setState({screen:"home"})}>place order</div>
					</div>
				</div>
	}

	renderFooter(){
		const cansubmit = checklitres(this.state.litres.trim()) && checkpostcode(this.state.postcode.trim());

		if (this.state.screen==="home"){
			return <div class={style.roundedbottom}>
					<div class={style.leftbottom}></div>
					<div class={style.rightbottom}>
						{cansubmit && this.renderGetQuote()}
					</div>
			</div>
		}
		if (this.state.screen==="quote"){
			return <div class={style.roundedbottom}>
					<div class={style.leftbottom}></div>
					<div class={style.rightbottom}>
						{this.acceptQuote()}
					</div>
			</div>
			
		}
		if (this.state.screen==="details"){
			return <div class={style.roundedbottom}>
				<div class={style.leftbottom}>
					<div class={style.footertext}>
						Once ordered we will contact you to arrange a delivery time
					</div>
				</div>
				<div class={style.rightbottom}>
					{this.placeOrder()}
				</div>
			</div>
			
		}
		return null;
	}

	renderGetQuote(){

		return <div class={style.button}>
					<div class={style.buttontext}>
						<div onClick={e=>this.setState({screen:"quote"})}>get my quote</div>
					</div>
				</div>
					
	}

	render(){
		
		return <div id={style.pricing}>
			<div class={style.containerbox}>
				<div class={style.imagebox}>
					{this.state.screen !== "details" && <div class={style.dropimage}></div>}
				</div>
				<div class={style.roundedtop}>
					<div class={style.lefttop}></div>
					<div class={style.righttop}>
						<div class={style.logo}></div>
					</div>
				</div>
				
				{this.renderContent()}

				
				{this.renderFooter()}
				
			</div>
		</div>
	}

}





